html {
    scroll-behavior: smooth;
}

body {
    /*max-width: 100vw;*/
    font-family: "Roboto", "Lucida Sans", "Candara";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 1rem;
    line-height: 1.15rem;
}

.slick-arrow {
    color: black;
    z-index: 10000;
}
.slick-arrow:before {
    color: black;
    z-index: 10000;
}
.prev{
    z-index: 10000;
    color: black;
    position:relative;
}
.next{
    z-index: 10000;
    color: black;
    position:relative;
}

.prev:before {
    z-index: 10000;
    /*noinspection CssNoGenericFontName*/
    font-family: FontAwesome;
    color: black;
    font-size: 5vmin;
    content: "\F177";
}

.next:before {
    z-index: 10000;
    font-size: 5vmin;
    color: black;
    /*noinspection CssNoGenericFontName*/
    font-family: FontAwesome;
    content: "\F178";
}


.slick-slider{
    -webkit-user-select: auto;
    -moz-user-select: auto;
    -ms-user-select: auto;
    user-select: auto;
}

.slick-slider .slick-list {
    -webkit-transform: none !important;
    transform: none !important;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media print {
    .no-print {
        display: none;
    }

    h1, h2, h3, h4, h5, h6 {
        page-break-after: avoid;
    }
}

@media print and (color) {
    * {
        -webkit-print-color-adjust: exact;
        print-color-adjust: exact;
    }
}

html {
    margin: 0;
    padding: 0;
    max-width: 100vw;
    overflow-x: hidden;
}

a {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

.carousel-item .carousel-caption {
    color: black;
    /*background-color: black;*/
}

.qrcode-container {
    display: block;
}

/* width */
::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}


@media screen and (max-width: 768px) {
    .contact-lists-container {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: flex-start;
                justify-content: flex-start;
    }

    .qrcode-container {
        display: none;
    }
}

@media print {
    a {
        text-decoration: none;
    }

    a:hover {
        text-decoration: none;
    }

    .contact-lists-container {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: space-between;
                justify-content: space-between;
    }

    .qrcode-container {
        display: block;
    }
}

