
html {
    margin: 0;
    padding: 0;
    max-width: 100vw;
    overflow-x: hidden;
}

a {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

.carousel-item .carousel-caption {
    color: black;
    /*background-color: black;*/
}

.qrcode-container {
    display: block;
}

/* width */
::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}


@media screen and (max-width: 768px) {
    .contact-lists-container {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
    }

    .qrcode-container {
        display: none;
    }
}

@media print {
    a {
        text-decoration: none;
    }

    a:hover {
        text-decoration: none;
    }

    .contact-lists-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    .qrcode-container {
        display: block;
    }
}
